
import { defineComponent } from "vue";
import store from "@/store";

interface LoginDetails {
  login: boolean;
  token: string;
  warningMessage: string;
}
export default defineComponent({
  name: "Login",
  data() {
    return {
      wrongPassword: false,
      password: ""
    };
  },
  methods: {
    login() {
      this.$http
        .post("/loginadmin", { password: this.password })
        .then(res => {
          const details = res.data as LoginDetails;
          if (
            details.warningMessage != null &&
            details.warningMessage.length > 0
          ) {
            store.state.warning = details.warningMessage;
          }

          if (details.login && details.token) {
            store.dispatch("loginAdmin", details.token);
            this.$router.push("/admin");
          } else {
            this.wrongPassword = true;
            this.password = "";
          }
        })
        .catch(console.error);
    }
  },
  mounted() {
    document.title = "Game Server | Login";
  }
});
