<template>
  <h1>Admin Login</h1>

  <template v-if="wrongPassword">
    <p>Wrong password</p>
  </template>

  <form @submit.prevent="login">
    <input
      type="password"
      name="password"
      v-model="password"
      placeholder="Admin password"
    />
    <button type="button" v-on:click="login()">
      Login
    </button>
  </form>

  <p style="margin-top:5em">
    <b>First time logging in?</b> <br />

    You can set/change the admin's password by passing it as an argument:
    <br />
    <br />
    <code> $ java -jar server.jar --set-password mynewpassword123!</code>
  </p>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import store from "@/store";

interface LoginDetails {
  login: boolean;
  token: string;
  warningMessage: string;
}
export default defineComponent({
  name: "Login",
  data() {
    return {
      wrongPassword: false,
      password: ""
    };
  },
  methods: {
    login() {
      this.$http
        .post("/loginadmin", { password: this.password })
        .then(res => {
          const details = res.data as LoginDetails;
          if (
            details.warningMessage != null &&
            details.warningMessage.length > 0
          ) {
            store.state.warning = details.warningMessage;
          }

          if (details.login && details.token) {
            store.dispatch("loginAdmin", details.token);
            this.$router.push("/admin");
          } else {
            this.wrongPassword = true;
            this.password = "";
          }
        })
        .catch(console.error);
    }
  },
  mounted() {
    document.title = "Game Server | Login";
  }
});
</script>
